<template>
  <div class="player-life">
      <div>
        <p class="name" v-if="!editingName" v-on:click="editName()">{{player.name}}</p>
        <input ref="playername" v-if="editingName"
            v-on:blur="hideEditName()" v-on:keyup.enter="hideEditName()"
            v-model="player.name" />
        <p class="life">{{player.life}}</p>

        <div class="life-btn" v-on:click="changeLife(player, -5)" id="minus5">-5</div>
        <div class="life-btn" v-on:click="changeLife(player, -1)" id="minus1">-1</div>
        <div class="life-btn" v-on:click="changeLife(player, 1)" id="plus1">+1</div>
        <div class="life-btn" v-on:click="changeLife(player, 5)" id="plus5">+5</div>
      </div>
  </div>
</template>

<script>
export default {
  name: 'PlayerLife',

  props: [
      'player'
  ],
  
  methods: {
      changeLife(player, amount) {
          player.life += amount;
      },

      editName() {
          this.editingName = true;
          this.$nextTick(() => {
              this.$refs.playername.focus();
          });
      },

      hideEditName() {
          this.editingName = false;
      }
  },

  data() {
      return {
          editingName: false,
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .player-life {
        font-size: 30px;
        width: 100%;
        height: 50%;
        text-align: center;
        display: flex;
        flex-direction: column;
        justify-content: center;
        font-family: Beleren, Helvetica, Arial, sans-serif;
    }

    p {
        margin: 0;
    }

    .life {
        font-size: 125px;
    }

    .name {
        font-size: 35px;
    }

    .life-btn {
        display: inline-block;
        border: 1px solid black;
        border-radius: 5px;
        padding: 5px 12px;
        margin: 5px 10px;
        cursor: pointer;
        user-select: none;
    }
</style>
